import React, { useState, useEffect } from 'react';
import { v4 as uuidv4 } from 'uuid';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import CircularProgress from '@mui/material/CircularProgress';
import Box from '@mui/material/Box';
import * as iscp from '@aptpod/iscp-ts'

const sleepMs = (ms: number) => new Promise<void>((resolve) => setTimeout(resolve, ms))
const getNowTimeNano = () => BigInt(Date.now()) * BigInt(1000_000)
const getRandomBytes = (length: number) => {
  const randomBytes = new Uint8Array(length);
  window.crypto.getRandomValues(randomBytes);
  return randomBytes;
}

const App: React.FC = () => {
    const [urlString, setUrlString] = useState(localStorage.getItem('url') || 'https://intdash-cluster-dev.intdash.jp');
    const [token, setToken] = useState(localStorage.getItem('token') || 'JKQu4HiTcQmAvu279QXusMwwzejGp5nFpIMIhXhiq8Gh9sSA.4kXTu7b81f6HihHBMS1cShDLHy4fjvEm3vuDJ4IoAEU');
    const [nodeId, setNodeId] = useState(localStorage.getItem('nodeId') || '29866475-8a4d-45cb-9be7-5abac4382c6c');
    const [dataName, setDataName] = useState(localStorage.getItem('dataName') || 'data_name');
    const [dataType, setDataType] = useState(localStorage.getItem('dataType') || 'bytes');
    const [payloadSize, setPayloadSize] = useState(localStorage.getItem('payloadSize') || '64');
    const [count, setCount] = useState(localStorage.getItem('count') || '10');
    const [interval, setInterval] = useState(localStorage.getItem('interval') || '1000');
    const [result, setResult] = useState('');
    const [isLoading, setIsLoading] = useState(false);

    useEffect(() => {
      localStorage.setItem('url', urlString);
    }, [urlString]);
    useEffect(() => {
      localStorage.setItem('token', token);
    }, [token]);
    useEffect(() => {
      localStorage.setItem('nodeId', nodeId);
    }, [nodeId]);
    useEffect(() => {
      localStorage.setItem('dataName', dataName);
    }, [dataName]);
    useEffect(() => {
      localStorage.setItem('dataType', dataType);
    }, [dataType]);
    useEffect(() => {
      localStorage.setItem('payloadSize', payloadSize);
    }, [payloadSize]);
    useEffect(() => {
      localStorage.setItem('count', count);
    }, [count]);
    useEffect(() => {
      localStorage.setItem('interval', interval);
    }, [interval]);

    const startProcess = async () => {
        setIsLoading(true);
        try {
            const tokenSource = async () => {
              const response = await fetch(`${urlString}/api/iscp/tickets`, {
                  method: 'POST',
                  headers: {'X-Intdash-Token': token},
              });
              if (!response.ok) {
                  throw new Error('Network response was not ok');
              }
              const data = await response.json();
              return data["ticket"];
            }

            const url = new URL(urlString);
            const enableTLS = url.protocol=="https:";
            const connector = new iscp.WebSocketConnector({enableTLS: enableTLS});

            const conn = await iscp.Conn.connect({
              address: url.hostname,
              connector,
              tokenSource,
              nodeId: nodeId,
            });

            const sessionId = uuidv4();
            const baseTime = getNowTimeNano();
            // FIXME: なぜかFailする
            // await conn.sendBaseTime(
            //   new iscp.BaseTime({
            //     name: 'EdgeRTC',
            //     elapsedTime: 0n,
            //     baseTime: baseTime,
            //     priority: 0,
            //     sessionId: sessionId,
            //   }),
            // );

            const upstream = await conn.openUpstream({
              sessionId: sessionId,
              qos: iscp.QOS.UNRELIABLE,
              flushPolicy: iscp.FlushPolicy.immediately(),
              persist: false,
            });
            const downstream = await conn.openDownstream({
              filters: [iscp.DownstreamFilter.allFor(nodeId)],
            });

            let outputText = '';
            downstream.addEventListener(iscp.Downstream.EVENT.CHUNK, (chunk) => {
              if (chunk.upstreamInfo.sessionId != sessionId) {
                return;
              }
              const rxTimeNano = getNowTimeNano();
              const txTimeNano = chunk.dataPointGroups[0].dataPoints[0].elapsedTime + baseTime;
              const rttNano = Number(rxTimeNano - txTimeNano);
              const rttMilli = rttNano / 1000000.0;
              outputText += `${chunk.sequenceNumber}: rtt=${rttMilli} ms<br/>`;
              setResult(outputText);
            });

            for (let i = 0; i < Number(count); i++) {
              await sleepMs(Number(interval))
              const payload = getRandomBytes(Number(payloadSize));
              await upstream.writeDataPoints(new iscp.DataId({name: dataName, type: dataType}), [
                new iscp.DataPoint({
                  elapsedTime: getNowTimeNano() - baseTime,
                  payload: payload,
                }),
              ]);
            }

        } catch (error) {
            console.log(error);
            setResult('通信に失敗しました。');

          } finally {
            setIsLoading(false);
        }
    };

    return (
      <Container maxWidth={false}>
        <Box sx={{ 
          display: 'flex', 
          flexDirection: 'column', 
          alignItems: 'center', 
          justifyContent: 'center', 
          minHeight: '100vh',
        }}>
          <Typography variant="h4" component="h1" gutterBottom>
            iSCP Ping
          </Typography>
          <TextField label="URL" variant="outlined" value={urlString} onChange={e => setUrlString(e.target.value)} margin="normal" />
          <TextField label="API Token" variant="outlined" value={token} onChange={e => setToken(e.target.value)} margin="normal" />
          <TextField label="Node ID" variant="outlined" value={nodeId} onChange={e => setNodeId(e.target.value)} margin="normal" />
          <TextField label="Data Name" variant="outlined" value={dataName} onChange={e => setDataName(e.target.value)} margin="normal" />
          <TextField label="Data Type" variant="outlined" value={dataType} onChange={e => setDataType(e.target.value)} margin="normal" />
          <TextField label="Payload Size" variant="outlined" value={payloadSize} onChange={e => setPayloadSize(e.target.value)} margin="normal" />
          <TextField label="Count" variant="outlined" value={count} onChange={e => setCount(e.target.value)} margin="normal" />
          <TextField label="Interval" variant="outlined" value={interval} onChange={e => setInterval(e.target.value)} margin="normal" />
          <Button variant="contained" color="primary" onClick={startProcess} disabled={isLoading} >
              {isLoading ? <CircularProgress size={24} /> : 'スタート'}
          </Button>
          {result && <Box sx={{ mt: 2 }}>{result}</Box>}
        </Box>
      </Container>
  );
};

export default App;
